<template>
	<view :class="{body:models=='1'||models=='', bodyNight:models=='0'}">
		<view class="content">
			<!-- 段落 -->
			<view class="sentence" v-html="data.content"></view>
			<view class="sentence">{{data.getsubject[0].title}}</view>
			<!-- 题目类型 -->
			<view class="info">
				<view class="head">
					<view class="head_left">
						<view class="type" v-if="data.getsubject[0].type==1">单选题</view>
						<view class="type" v-else>多选题</view>
					</view>
					<view class="head_right">	
						<view v-if="wrong==1">
							<u-icon name="trash" color="#cbcbcb" labelColor="#cbcbcb" size="28px" labelSize="10px" label="点击删除" labelPos="bottom" @click="sureDelete"></u-icon>
						</view>
						<view v-else>
							<u-icon name="warning" color="#cbcbcb" labelColor="#cbcbcb" size="28px" labelSize="10px" label="点击报错" labelPos="bottom" @click="toErr"></u-icon>
							<u-icon name="star-fill" v-if="data[current].is_collection==0" color="#cbcbcb" labelColor="#cbcbcb" size="28px" labelSize="10px" label="点击收藏" labelPos="bottom" @click="setCollect"></u-icon>
							<u-icon name="star-fill" v-else="data[current].is_collection==1" color="#ffd656" labelColor="#cbcbcb" size="28px" labelSize="10px" label="点击收藏" labelPos="bottom" @click="setCollect"></u-icon>
						</view>
					</view>
				</view>
			</view>
		</view>
		<!-- 单选类型 -->
		<view v-if="data.getsubject[0].type=='1'" class="single_choice">
			<!-- 答案 -->
			<view class="answerList" v-if="isSee==false">
				<view class="answer" v-for="(item,index) in data.getsubject[0].options" :key="index" @click="confirm(index)">
					<view v-if="index==0" :class="{serial:true, checked:item.isChecd} ">A</view>
					<view v-else-if="index==1" :class="{serial:true, checked:item.isChecd} ">B</view>
					<view v-else-if="index==2" :class="{serial:true, checked:item.isChecd} ">C</view>
					<view v-else-if="index==3" :class="{serial:true, checked:item.isChecd} ">D</view>
					<view v-else-if="index==4" :class="{serial:true, checked:item.isChecd} ">E</view>
					<view v-else-if="index==5" :class="{serial:true, checked:item.isChecd} ">F</view>
					<view v-else-if="index==6" :class="{serial:true, checked:item.isChecd} ">G</view>
					<view v-else-if="index==7" :class="{serial:true, checked:item.isChecd} ">H</view>
					<view class="text">{{item.option}}</view>
				</view>
				<button :class="{checkaswer:answer}" @click="seeResolution">确定</button>
			</view>
			<!-- 答案 -->
			<view class="answerList" v-else>
				<view class="answer" v-for="(item,index) in data.getsubject[0].options" :key="index">
					<view v-if="index==0" :class="{serial:true, trueAnswer:item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">A</view>
					<view v-else-if="index==1" :class="{serial:true, trueAnswer:item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">B</view>
					<view v-else-if="index==2" :class="{serial:true, trueAnswer:item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">C</view>
					<view v-else-if="index==3" :class="{serial:true, trueAnswer:item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">D</view>
					<view v-else-if="index==4" :class="{serial:true, trueAnswer:item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">E</view>
					<view v-else-if="index==5" :class="{serial:true, trueAnswer:item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">F</view>
					<view v-else-if="index==6" :class="{serial:true, trueAnswer:item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">G</view>
					<view v-else-if="index==7" :class="{serial:true, trueAnswer:item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">H</view>
					<view class="text">{{item.option}}</view>
				</view>
			</view>
		</view>
		<!-- 多选类型 -->
		<view v-else-if="data.getsubject[0].type=='2'" class="multiple_choice">
			<!-- 答案 -->
			<view class="answerList" v-if="isSee==false">
				<view class="answer" v-for="(item,index) in data.getsubject[0].options" :key="index" @click="confirms(index)">
					<view v-if="index==0" :class="{serial:true, checked:item.isChecd} ">A</view>
					<view v-else-if="index==1" :class="{serial:true, checked:item.isChecd} ">B</view>
					<view v-else-if="index==2" :class="{serial:true, checked:item.isChecd} ">C</view>
					<view v-else-if="index==3" :class="{serial:true, checked:item.isChecd} ">D</view>
					<view v-else-if="index==4" :class="{serial:true, checked:item.isChecd} ">E</view>
					<view v-else-if="index==5" :class="{serial:true, checked:item.isChecd} ">F</view>
					<view v-else-if="index==6" :class="{serial:true, checked:item.isChecd} ">G</view>
					<view v-else-if="index==7" :class="{serial:true, checked:item.isChecd} ">H</view>
					<view class="text">{{item.option}}</view>
				</view>
				<button :class="{checkaswer:answerList.length!=0}" @click="seeResolution">确定</button>
			</view>
			<!-- 答案 -->
			<view class="answerList" v-else>
				<view class="answer" v-for="(item,index) in data.getsubject[0].options" :key="index">
					<view v-if="index==0" :class="{serial:true, trueAnswer:item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">A</view>
					<view v-else-if="index==1" :class="{serial:true, trueAnswer:item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">B</view>
					<view v-else-if="index==2" :class="{serial:true, trueAnswer:item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">C</view>
					<view v-else-if="index==3" :class="{serial:true, trueAnswer:item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">D</view>
					<view v-else-if="index==4" :class="{serial:true, trueAnswer:item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">E</view>
					<view v-else-if="index==5" :class="{serial:true, trueAnswer:item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">F</view>
					<view v-else-if="index==6" :class="{serial:true, trueAnswer:item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">G</view>
					<view v-else-if="index==7" :class="{serial:true, trueAnswer:item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">H</view>
					<view class="text">{{item.option}}</view>
				</view>
			</view>
		</view>
		<!-- 答案对比 -->
		<view v-show="isSee" class="answerProofread">
			<view class="trueA">正确答案:
				<view>{{trueAswer}}</view>
				
			</view>
			<view :class="{falseA:answerState==false, trueA:answerState==true}">你的答案:
				<view>{{userAswer}}</view>
			</view>
		</view>
		<!-- 图片 -->
		<view v-if="data.cover" v-show="isSee" class="answerAnalysis">
			<img :src="data.cover" alt="">
		</view>
		<!-- 答案解析 -->
		<view v-if="data.getsubject[0].analysis" v-show="isSee" class="answerAnalysis">
			<view class="title">解析</view>
			<view class="content" v-html="data.getsubject[0].analysis"></view>
		</view>
		<view v-if="isSee" class="buttons">
			<button class="nextNub" @click="overTime">完成</button>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import { currentDetails } from '@/common/http.api.js';
	export default{
		data() {
			return {
				// unitId:'',
				isSee:false, //解析显示状态
				type:'', //题目类型
				answer:'', //单选答案
				answerList:[], //多选答案
				wrong:0, //判断错题进入
				uid:'',
				token:'',
				models:'1', //白天模式
				trueAswer:'',
				userAswer:'',
				answerState:false, //答案是否正确
				data:{}
			}
		},
		onLoad(option) {
			this.videoContext = uni.createVideoContext('videos')
			this.models = store.state.model
			this.token = store.state.vuex_token
			this.uid = store.state.vuex_user.student_id
			if(this.models=='0'){
				console.log('进入夜间模式')
				uni.setNavigationBarColor({
				    frontColor: '#ffffff',
				    backgroundColor: '#303030',
				    animation: {
				        duration: 0,
				        timingFunc: 'easeIn'
				    }
				})
			}else{
				console.log('进入白天模式')
				uni.setNavigationBarColor({
				    frontColor: '#ffffff',
				    backgroundColor: '#6ab4ff',
				    animation: {
				        duration: 0,
				        timingFunc: 'easeIn'
				    }
				})
			}
			currentDetails({uid:this.uid, id:option.id, token:this.token}).then(res=>{
				console.log(res)
				this.data = res.data.data
				for(let i in this.data.getsubject[0].options){
					console.log(this.data.getsubject[0].options[i].answer)
					if(this.data.getsubject[0].options[i].answer == 'true'){
						if(i==0){
							this.trueAswer= this.trueAswer+'A'
						}else if(i==1){
							this.trueAswer.length==0 ? this.trueAswer= this.trueAswer+'B' : this.trueAswer= this.trueAswer+',B'
						}else if(i==2){
							this.trueAswer.length==0 ? this.trueAswer= this.trueAswer+'C' : this.trueAswer= this.trueAswer+',C'
						}else if(i==3){
							this.trueAswer.length==0 ? this.trueAswer= this.trueAswer+'D' : this.trueAswer= this.trueAswer+',D'
						}else if(i==4){
							this.trueAswer.length==0 ? this.trueAswer= this.trueAswer+'E' : this.trueAswer= this.trueAswer+',E'
						}else if(i==5){
							this.trueAswer.length==0 ? this.trueAswer= this.trueAswer+'F' : this.trueAswer= this.trueAswer+',F'
						}else if(i==6){
							this.trueAswer.length==0 ? this.trueAswer= this.trueAswer+'G' : this.trueAswer= this.trueAswer+',G'
						}else if(i==7){
							this.trueAswer.length==0 ? this.trueAswer= this.trueAswer+'H' : this.trueAswer= this.trueAswer+',H'
						}
					}
				}
				console.log(this.trueAswer)
				uni.setNavigationBarTitle({
					title: this.data.title
				});
			}).catch(err=>{
				console.log(err)
			})
		},
		methods: {
			// 单选 选择答案
			confirm(e) {
				console.log(e)
				for(let i in this.data.getsubject[0].options){
					this.data.getsubject[0].options[i].isChecd = false
				}
				this.data.getsubject[0].options[e].isChecd = true
				console.log(this.data.getsubject[0].options)
				console.log(this.data.getsubject[0].options[e].isChecd)
				this.answer = this.data.getsubject[0].options[e].option
			},
			// 多选 选择答案
			confirms(e) {
				console.log(e)
				this.data.getsubject[0].options[e].isChecd = true
				console.log(this.data.getsubject[0].options)
				console.log(this.data.getsubject[0].options[e].isChecd)
				this.answerList.push(this.data.getsubject[0].options[e].option)
			},
			// 完成
			overTime() {
				if(this.isSee==true){
					uni.navigateBack({
						delta:1
					})
				}else{
					this.$u.toast('还没错题哦~')
				}
			},
			// 查看解析
			seeResolution() {
				if(this.type=='single_choice'){
					//单选解析
					if(this.answer){
						for(let i in this.data.getsubject[0].options){
							if(this.data.getsubject[0].options[i].isChecd == true){
								if(i==0){
									this.userAswer= this.userAswer+'A'
								}else if(i==1){
									this.userAswer.length==0 ? this.userAswer= this.userAswer+'B' : this.userAswer= this.userAswer+',B'
								}else if(i==2){
									this.userAswer.length==0 ? this.userAswer= this.userAswer+'C' : this.userAswer= this.userAswer+',C'
								}else if(i==3){
									this.userAswer.length==0 ? this.userAswer= this.userAswer+'D' : this.userAswer= this.userAswer+',D'
								}else if(i==4){
									this.userAswer.length==0 ? this.userAswer= this.userAswer+'E' : this.userAswer= this.userAswer+',E'
								}else if(i==5){
									this.userAswer.length==0 ? this.userAswer= this.userAswer+'F' : this.userAswer= this.userAswer+',F'
								}else if(i==6){
									this.userAswer.length==0 ? this.userAswer= this.userAswer+'G' : this.userAswer= this.userAswer+',G'
								}else if(i==7){
									this.userAswer.length==0 ? this.userAswer= this.userAswer+'H' : this.userAswer= this.userAswer+',H'
								}
							}
						}
						this.trueAswer == this.userAswer ? this.answerState = true :false
						this.isSee = true
						let answers = []
						let id = store.state.subject_id //大题ID
						this.data.getsubject[0].options.map((item)=>{
							if(item.isChecd==true){
								return answers.push({answer:item.answer, option:item.option, result:true}) 
							}else{
								answers.push({answer:item.answer, option:item.option, result:null}) 
							}
						})
						let answer = JSON.stringify(answers)
						console.log("-----")
						console.log(answers)
						console.log(answer)
						let params = {
							answer: answer,
							answer_type:this.data.type,
							is_end:this.data.is_end,
							option_id:this.data.id,
							subject_id:id,
							token:this.token
						}
						/* checkAnswer({...params}).then(res=>{
							console.log(res)
							let id = store.state.subject_id //大题ID
							subject({token:this.token,subject_id:id,order:this.orders-1}).then(res=>{
								console.log(res)
								this.data = res.data.data
								this.orders = res.data.data.subject_order
								console.log(this.data)
							}).catch(err=>{
								console.log(err)
							})
						}).catch(err=>{
							console.log(err)
						}) */
					}else{
						this.$u.toast("先尝试一下吧~")
					}
				}else{
					//多选解析
					if(this.answerList.length!=0){
						for(let i in this.data.getsubject[0].options){
							if(this.data.getsubject[0].options[i].isChecd == true){
								if(i==0){
									this.userAswer= this.userAswer+'A'
								}else if(i==1){
									this.userAswer.length==0 ? this.userAswer= this.userAswer+'B' : this.userAswer= this.userAswer+',B'
								}else if(i==2){
									this.userAswer.length==0 ? this.userAswer= this.userAswer+'C' : this.userAswer= this.userAswer+',C'
								}else if(i==3){
									this.userAswer.length==0 ? this.userAswer= this.userAswer+'D' : this.userAswer= this.userAswer+',D'
								}else if(i==4){
									this.userAswer.length==0 ? this.userAswer= this.userAswer+'E' : this.userAswer= this.userAswer+',E'
								}else if(i==5){
									this.userAswer.length==0 ? this.userAswer= this.userAswer+'F' : this.userAswer= this.userAswer+',F'
								}else if(i==6){
									this.userAswer.length==0 ? this.userAswer= this.userAswer+'G' : this.userAswer= this.userAswer+',G'
								}else if(i==7){
									this.userAswer.length==0 ? this.userAswer= this.userAswer+'H' : this.userAswer= this.userAswer+',H'
								}
							}
						}
						this.trueAswer == this.userAswer ? this.answerState = true :false
						this.isSee = true
						let answers = []
						let id = store.state.subject_id //大题ID
						this.data.getsubject[0].options.map((item)=>{
							if(item.isChecd==true){
								return answers.push({answer:item.answer, option:item.option, result:true}) 
							}else{
								answers.push({answer:item.answer, option:item.option, result:null}) 
							}
						})
						let answer = JSON.stringify(answers)
						console.log("-----")
						console.log(answers)
						console.log(answer)
						let params = {
							answer: answer,
							answer_type:this.data.type,
							is_end:this.data.is_end,
							option_id:this.data.id,
							subject_id:id,
							token:this.token
						}
						/* checkAnswer({...params}).then(res=>{
							console.log(res)
							let id = store.state.subject_id //大题ID
							subject({token:this.token,subject_id:id,order:this.orders-1}).then(res=>{
								console.log(res)
								this.data = res.data.data
								this.orders = res.data.data.subject_order
								console.log(this.data)
							}).catch(err=>{
								console.log(err)
							})
						}).catch(err=>{
							console.log(err)
						}) */
					}else{
						this.$u.toast("先尝试一下吧~")
					}
				}
			}
		}
	}
</script>

<style lang="scss">
	.info{
		.head{
			display: flex;
			justify-content: space-between;
			align-items: center;
			.head_left{
				display: flex;
				line-height: 44rpx;
				font-size: 24rpx;
				height: 44rpx;
				.type{
					background-color: #6ab4ff;
					color: #fff;
					width: 100rpx;
					border-radius: 6rpx;
					text-align: center;
					margin-right: 20rpx;
				}
			}
			.head_right{
				view{
					display: flex;
					view{
						margin-right: 20rpx;
					}
				}
			}
		}
	}
	.answerProofread{
		display: flex;
		justify-content: space-between;
		padding: 0 40rpx;
		margin-bottom: 40rpx;
		.trueA{
			view{
				display: inline-block;
				color: #50d891;
			}
		}
		.falseA{
			view{
				display: inline-block;
				color: #ff6227;
			}
		}
	}
	.buttons{
		position: fixed;
		bottom: 0rpx;
		left: 0;
		width: 90%;
		padding: 20rpx 5%;
		display: flex;
		justify-content: space-between;
		.nextNub{
			background-color: #ff8d8a;
			color: #fff;
			flex: 5;
			line-height: 78rpx;
			border-radius: 100rpx;
		}
	}
	// 单选类型
	.single_choice{
		.answerList{
			margin: 40rpx 30rpx;
			.answer{
				margin: 30rpx 0;
				display: flex;
				.serial{
					margin-right: 20rpx;
					border: 1px solid #cbcbcb;
					border-radius: 50%;
					color: #cbcbcb;
					width: 70rpx;
					height: 70rpx;
					line-height: 70rpx;
					text-align: center;
				}
				.checked{
					border: 1px solid #50d891;
					color: #50d891;
				}
				.trueAnswer{
					border: 1px solid #50d891;
					background-color: #50d891;
					color: #fff;
				}
				.falseAnswer{
					border: 1px solid #ff6227;
					background-color: #ff6227;
					color: #fff;
				}
				.text{
					line-height: 70rpx;
					flex: 6;
				}
			}
			button{
				padding: 0;
				color: #fff;
				width: 300rpx;
				border-radius: 50rpx;
				font-size: 28rpx;
				background-color: #cbcbcb !important;
				background: none;
				margin-bottom: 40rpx;
			}
			.checkaswer{
				background-color: #6ab4ff !important;
			}
		}
	}
	// 多选类型
	.multiple_choice{
		.answerList{
			margin: 40rpx 30rpx;
			.answer{
				margin: 30rpx 0;
				display: flex;
				.serial{
					margin-right: 20rpx;
					border: 1px solid #cbcbcb;
					border-radius: 50%;
					color: #cbcbcb;
					width: 70rpx;
					height: 70rpx;
					line-height: 70rpx;
					text-align: center;
				}
				.checked{
					border: 1px solid #50d891;
					color: #50d891;
				}
				.trueAnswer{
					border: 1px solid #50d891;
					background-color: #50d891;
					color: #fff;
				}
				.falseAnswer{
					border: 1px solid #ff6227;
					background-color: #ff6227;
					color: #fff;
				}
				.text{
					line-height: 70rpx;
					flex: 6;
				}
			}
			button{
				padding: 0;
				color: #fff;
				width: 300rpx;
				border-radius: 50rpx;
				font-size: 28rpx;
				background-color: #cbcbcb !important;
				background: none;
				margin-bottom: 40rpx;
			}
			.checkaswer{
				background-color: #6ab4ff !important;
			}
		}
	}
	.answerAnalysis{
		margin: 30rpx 40rpx;
		img{
			width: 100%;
		}
		.title::before{
			content: '';
			display: inline-block;
			width: 10rpx;
			height: 30rpx;
			background-color: #6ab4ff;
			border-radius: 50rpx;
			margin-right: 20rpx;
		}
		.title{
